// Button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons

.button-variant-base(@color; @background; @border) {
  color: @color;
  background-color: @background;
  border-color: @border;

  &:active,
  &.active,
  .open > .dropdown-toggle& {
    background-image: none;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus,
    &.focus {
      background-color: @background;
          border-color: @border;
    }
  }

  .badge {
    color: @background;
    background-color: @color;
  }
}

.button-variant(@color; @background; @border) {
  .button-variant-base(@color; @background; @border);
  &:focus,
  &.focus {
    color: @color;
    background-color: darken(@background, 10%);
        border-color: darken(@border, 25%);
  }
  &:hover {
    color: @color;
    background-color: darken(@background, 10%);
        border-color: darken(@border, 12%);
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    color: @color;
    background-color: darken(@background, 10%);
        border-color: darken(@border, 12%);

    &:hover,
    &:focus,
    &.focus {
      color: @color;
      background-color: darken(@background, 17%);
          border-color: darken(@border, 25%);
    }
  }
}

.button-variant-lighten(@color; @background; @border) {
  .button-variant-base(@color; @background; @border);
  &:focus,
  &.focus {
    color: @color;
    background-color: lighten(@background, 20%);
        border-color: lighten(@border, 45%);
  }
  &:hover {
    color: @color;
    background-color: lighten(@background, 20%);
        border-color: lighten(@border, 24%);
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    color: @color;
    background-color: lighten(@background, 20%);
        border-color: lighten(@border, 24%);

    &:hover,
    &:focus,
    &.focus {
      color: @color;
      background-color: lighten(@background, 34%);
          border-color: lighten(@border, 50%);
    }
  }
}
// Button sizes
.button-size(@padding-vertical; @padding-horizontal; @font-size; @line-height; @border-radius) {
  padding: @padding-vertical @padding-horizontal;
  font-size: @font-size;
  line-height: @line-height;
  border-radius: @border-radius;
}

.button-icon(@icon; @font-family; @font-size) {
  .button-icon-base(@icon; @font-family; @font-size; bold);
  margin-left: 10px;
}

.button-icon-absolute(@icon; @font-family; @font-size) {
  .button-icon-base(@icon; @font-family; @font-size; normal);
  position: absolute;
}

.button-icon-base(@icon; @font-family; @font-size; @font-weight) {
  content: @icon;
  font-family: @font-family;
  font-size: @font-size;
  font-style: normal;
  font-weight: @font-weight;
  font-variant: normal;
  line-height: 1;
  text-transform: none;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}