//
// Navs
// --------------------------------------------------


// Base class
// --------------------------------------------------

.nav {
  margin-bottom: 0;
  padding-left: 0; // Override default ul/ol
  list-style: none;
  &:extend(.clearfix all);

  > li {
    position: relative;
    display: block;

    > a {
      position: relative;
      display: block;
      padding: @nav-link-padding;
      &:hover,
      &:focus {
        text-decoration: none;
      }
    }

    // Disabled state sets text to gray and nukes hover/tab effects
    &.disabled > a {
      color: @nav-disabled-link-color;

      &:hover,
      &:focus {
        color: @nav-disabled-link-hover-color;
        text-decoration: none;
        background-color: transparent;
        cursor: @cursor-disabled;
      }
    }
  }

  // Open dropdowns
  /*.open > a {
    &,
    &:hover,
    &:focus {
      background-color: @nav-link-hover-bg;
      border-color: @link-color;
    }
  }*/

  // Nav dividers (deprecated with v3.0.1)
  //
  // This should have been removed in v3 with the dropping of `.nav-list`, but
  // we missed it. We don't currently support this anywhere, but in the interest
  // of maintaining backward compatibility in case you use it, it's deprecated.
  .nav-divider {
    .nav-divider();
  }

  // Prevent IE8 from misplacing imgs
  //
  // See https://github.com/h5bp/html5-boilerplate/issues/984#issuecomment-3985989
  > li > a > img {
    max-width: none;
  }
}


// Tabs
// -------------------------

// Give the tabs something to sit on
.nav-tabs {
  border-bottom: 2px solid @nav-tabs-border-color;
  > li {
    display: inline-block;
    vertical-align: bottom; 
    
    //float: left;
    // Make the list-items overlay the bottom border
    //margin-bottom: -1px;

    // Actual tabs (as links)
    > a {
      background-color: @black;
      border: 1px solid transparent;
      border-radius: @border-radius-base @border-radius-base 0 0;
      color: @white;
      margin-right: 2px;
      line-height: @line-height-base;
      padding: @nav-tabs-link-padding;

      &:hover {
        background-color: @nav-tabs-active-link-hover-bg;
      }

      &:focus {
        background-color: lighten(@nav-tabs-active-link-hover-bg, 20%);
      }
    }

    // Active state, and its :hover to override normal :hover
    &.active > a {
      padding: @nav-tabs-link-active-padding;
      background-color: @nav-tabs-active-link-hover-bg;
      
      &,
      &:hover,
      &:focus {
        cursor: default;
      }
    }
  }

  &.animated {
    > li {
      > a {
        transition: .15s padding ease-in-out;
      }

      &:not(.active) > a {
        transition-delay: .15s;
      }
    }
  }
  // pulling this in mainly for less shorthand
  &.nav-justified {
    .nav-justified();
    .nav-tabs-justified();
  }
}


// Pills
// -------------------------
.nav-pills {
  > li {
    float: left;

    &:after {
      content: "/";
      float: right;
    }

    &:last-child {
      &:after {
        content: "";
      }
    }

    // Links rendered as pills
    > a {
      color: @black;
      display: inline;

      &:hover {
        color: @brand-primary;
      }
    }
    + li {
      margin-left: 2px;
    }

    // Active state
    &.active > a {
      &,
      &:hover,
      &:focus {
        color: @nav-pills-active-link-hover-color;
        font-family: @corporate-s-bold-regular;
      }
    }
  }
}


// Stacked pills
.nav-stacked {
  background: @cool-grey-primary-color;
  padding: @padding-large-horizontal 0 @padding-large-horizontal @padding-large-horizontal;

  > li {
    float: none;
    position: relative;
    + li {
      margin-top: 2px;
      margin-left: 0; // no need for this gap between nav items
    }    

    &:after {
      content: "";
    }

    > a {
      display: block;
      &:hover {
        color: @cool-grey-quaternary-color;
      }
    }

    // Nested list has been opened
    &.open {
       > a {
         background-color: transparent;
         font-family: @corporate-s-bold-regular;

         &:hover,
         &:focus {
           background-color: transparent;
         }
       }
    }

    // Active state
    &.active > a {
      &,
      &:hover,
      &:focus {
        background: @black;
        color: @white;
        font-family: @corporate-s-regular;
      }

      &:after {
        left: 100%;
        top: 50%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-color: rgba(0, 0, 0, 0);
        border-left-color: #000000;
        border-width: 20px;
        margin-top: -20px;
      }
    }
  }
  // Nested lists
  .nav-stacked {
    padding: 0;

    > li {
      > a {
        padding-left: (@padding-large-horizontal * 2);
      }
    }
  }
}


// Nav variations
// --------------------------------------------------

// Justified nav links
// -------------------------

.nav-justified {
  width: 100%;

  > li {
    float: none;
    > a {
      text-align: center;
      margin-bottom: 5px;
    }
  }

  > .dropdown .dropdown-menu {
    top: auto;
    left: auto;
  }

  @media (min-width: @screen-sm-min) {
    > li {
      display: table-cell;
      width: 1%;
      > a {
        margin-bottom: 0;
      }
    }
  }
}

// Move borders to anchors instead of bottom of list
//
// Mixin for adding on top the shared `.nav-justified` styles for our tabs
.nav-tabs-justified {
  border-bottom: 0;

  > li > a {
    // Override margin from .nav-tabs
    margin-right: 0;
    border-radius: @border-radius-base;
  }
  /*
  > .active > a,
  > .active > a:hover,
  > .active > a:focus {
    border: 1px solid @nav-tabs-justified-link-border-color;
  }

  @media (min-width: @screen-sm-min) {
    > li > a {
      border-bottom: 1px solid @nav-tabs-justified-link-border-color;
      border-radius: @border-radius-base @border-radius-base 0 0;
    }
    > .active > a,
    > .active > a:hover,
    > .active > a:focus {
      border-bottom-color: @nav-tabs-justified-active-link-border-color;
    }
  }*/
}


// Tabbable tabs
// -------------------------

// Hide tabbable panes to start, show them when `.active`
.tab-content {
  > .tab-pane {
    display: none;
  }
  > .active {
    display: block;
  }
}


// Dropdowns
// -------------------------

// Specific dropdowns
.nav-tabs .dropdown-menu {
  // make dropdown border overlap tab border
  margin-top: -1px;
  // Remove the top rounded corners here since there is a hard edge above the menu
  .border-top-radius(0);
}
