//
// Pagination (multiple pages)
// --------------------------------------------------
.pagination {
  display: inline-block;
  padding-left: 0;
  margin: @line-height-computed 0;
  //border-radius: @border-radius-base;

  > li {
    display: inline; // Remove list-style and block-level defaults
    > a,
    > span {
      position: relative;
      float: left; // Collapse white-space
      padding: @padding-base-vertical @padding-base-horizontal;
      line-height: @line-height-base;
      text-decoration: none;
      color: @pagination-color;
      background-color: @pagination-bg;
      border: 1px solid @pagination-border;
      margin-left: -1px;
    }
    &:first-child {
      > a,
      > span {
        margin-left: 0;
        //.border-left-radius(@border-radius-base);
      }
    }
    /*&:last-child {
      > a,
      > span {
        .border-right-radius(@border-radius-base);
      }
    }*/
  }

  > li > a,
  > li > span {
    &:hover,
    &:focus {
      z-index: 2;
      color: @pagination-hover-color;
      background-color: @pagination-hover-bg;
      border-color: @pagination-hover-border;
    }
  }

  > .active > a,
  > .active > span {
    &,
    &:hover,
    &:focus {
      z-index: 3;
      color: @pagination-active-color;
      background-color: @pagination-active-bg;
      border-color: @pagination-active-border;
      cursor: default;
    }
  }

  > .disabled {
    > span,
    > span:hover,
    > span:focus,
    > a,
    > a:hover,
    > a:focus {
      color: @pagination-disabled-color;
      background-color: @pagination-disabled-bg;
      border-color: @pagination-disabled-border;
      cursor: @cursor-disabled;
    }
  }

  &.secondary {
    > li {
      > a,
      > span {
        border: 1px solid @white;
        color: @black;
      }

      &:first-child,
      &:last-child {
        > a,
        > span {
          background-color: @black; 
          color: @white;
          padding: 6px;

          &:hover,
          &:focus {
            background-color: @black; 
            color: darken(@white, 40%);
          }
        }
      }
    }

    > li > a,
    > li > span {
      &:hover,
      &:focus {
        background-color: transparent;
        color: lighten(@black, 70%);
      }
    }
    
    > .active > a,
    > .active > span {
      &,
      &:hover,
      &:focus {
        color: lighten(@black, 70%);
        background-color: transparent;
      }
    }

    > .disabled {
      > span,
      > span:hover,
      > span:focus,
      > a,
      > a:hover,
      > a:focus {
        color: lighten(@black, 70%);;
        cursor: @cursor-disabled;
      }
    }

    &:first-child,
    &:last-child {
      > .disabled {
        > span,
        > span:hover,
        > span:focus,
        > a,
        > a:hover,
        > a:focus {
          color: darken(@white, 40%);
          background-color: lightn(@black, 50%);
        }
      }
    }
  }
}

// Sizing
// --------------------------------------------------

// Large
.pagination-lg {
  .pagination-size(@padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; 0);
}

// Small
.pagination-sm {
  .pagination-size(@padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; 0);
}
